@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

:root {
  --black: #1B1B1B;
  --white: #F5F5F5;
  --ms-green: rgb(151, 215, 0);
  font-size: 16px;
  font-family: 'Poppins', sans-serif;
}
.exercise01 {
  background-color: var(--black);
  color: var(--white);
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
}

.movies__list, .movies__cart {
  width: 24em;
}

.movies__list {
  background-color: #222222;
  padding: 1em;
  border-radius: .2em;
  margin-right: 2em;
}

.movies__list-card {
  background-color: #333333;
  margin-bottom: 1rem;
  padding: .75em;
  border-radius: .2em;
}

.movies__list-card:last-child {
  margin-bottom: 0;
}

.movies__list-card ul li {
  font-size: 1em;
  line-height: 120%;
}

.movies__list-card button {
  background-color: var(--ms-green);
  border: none;
  cursor: pointer;
  font-size: .75em;
  border-radius: .25em;
  margin-top: .75em;
  padding: .25em .5em;
}

.movies__cart {
  background-color: #222222;
  padding: 1em;
  border-radius: .2em;
}

.movies__cart-card {
  background-color: #333333;
  margin-bottom: 1rem;
  padding: .75em;
  border-radius: .2em;
}

.movies__cart-card:last-child {
  margin-bottom: 0;
}

.movies__cart-card ul li {
  font-size: 1em;
  line-height: 120%;
}

.movies__cart-card-quantity {
  display: flex;
  align-items: center;
  margin-top: .5em;
}

.movies__cart-card span {
  margin: 0 .5em;
}

.movies__cart-card button {
  background-color: var(--ms-green);
  border: none;
  cursor: pointer;
  font-size: .75em;
  border-radius: .25em;
  padding: .25em .5em;
}

.movies__cart-total {
  margin-top: 1em;
  font-size: 1.25em;
}
